/* Define grid areas */
.Main {
  grid-area: main; }

.Header {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  display: -ms-flexbox;
  display: flex;
  grid-area: header;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 15px;
  text-align: center; }
  .Header .Icon {
    height: 30px; }

.Footer {
  -ms-flex-align: center;
      align-items: center;
  background-color: #646464;
  color: #FFFFFF;
  display: -ms-flexbox;
  display: flex;
  grid-area: footer;
  -ms-flex-pack: center;
      justify-content: center; }

.App {
  background: url("./core/assets/isometric-bg.png");
  background-size: cover;
  display: grid;
  grid-template-areas: "header" "main" "footer";
  grid-template-columns: 100%;
  grid-template-rows: 48px 1fr 48px;
  min-height: 100vh; }

@media screen and (max-width: 600px) {
  .App {
    grid-template-areas: "header" "main" "footer";
    grid-template-columns: 100%;
    grid-template-rows: 48px 1fr 48px; } }



